body {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  margin: 0;
  font-family: sans-serif;
  font-size: 24px;
  font-weight: 900;
  background: #fbf8ef;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 450px;
}

.game-header {
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 24px;
  padding: 0 20px;

  @media (min-width: 500px) {
    padding: 0 10px;
  }
}

.title {
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 65px;
  height: 65px;
  margin: 0;
  font-size: 24px;
  color: #f9f6f2;
  border-radius: 5px;
  background: #edc22e;

  @media (min-width: 500px) {
    width: 75px;
    height: 75px;
  }
}

.controls {
  display: flex;
}

.info {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 65px;
  height: 65px;
  margin: 0 8px 0 0;
  color: #776e65;
  font-size: 16px;
  border-radius: 5px;
  background: #d6cdc4;

  @media (min-width: 500px) {
    width: 75px;
    height: 75px;
  }
}

.button {
  width: 65px;
  height: 65px;
  font-family: sans-serif;
  font-weight: 700;
  font-size: 16px;
  color: #f9f6f2;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  outline: none;
  transition: background 0.25s ease;

  @media (min-width: 500px) {
    width: 75px;
    height: 75px;
  }
}

.start {
  font-size: 20px;
  background: #1dae28;

  &:hover {
    background: #179921;
  }
}

.restart {
  background: #f1b2b2;

  &:hover {
    background: #f87474;
  }
}

.game-field {
  background: #bbada0;
  border-spacing: 10px;
  border-radius: 5px;
}

.field-cell {
  box-sizing: border-box;
  width: 20vw;
  height: 20vw;
  text-align: center;
  color: #776e65;
  background: #d6cdc4;
  border-radius: 5px;
  user-select: none;

  @media (min-width: 500px) {
    width: 100px;
    height: 100px;
    font-size: 28px;
  }

  &--2 {
    background: #eee4da;
  }

  &--4 {
    background: #ede0c8;
  }

  &--8 {
    background: #f2b179;
    color: #f9f6f2;
  }

  &--16 {
    background: #f59563;
    color: #f9f6f2;
  }

  &--32 {
    background: #f67c5f;
    color: #f9f6f2;
  }

  &--64 {
    background: #f65e3b;
    color: #f9f6f2;
  }

  &--128 {
    background: #edcf72;
    color: #f9f6f2;
  }

  &--256 {
    background: #edcc61;
    color: #f9f6f2;
  }

  &--512 {
    background: #edc850;
    color: #f9f6f2;
  }

  &--1024 {
    background: #edc53f;
    color: #f9f6f2;
  }

  &--2048 {
    background: #edc22e;
    color: #f9f6f2;
  }
}

.message-container {
  box-sizing: border-box;
  width: 100%;
  height: 150px;
  padding: 10px;
}

.message {
  box-sizing: border-box;
  width: 100%;
  padding: 10px;
  font-size: 20px;
  text-align: center;
  color: #776e65;
  border-radius: 5px;
  background: #d6cdc4;
}

.hidden {
  display: none;
}

.message-win {
  color: #f9f6f2;
  background: #edc22e;
}
